import { Component, OnInit } from '@angular/core';

import { EditorService } from 'app/editor/editor.service';

@Component({
  selector: 'app-modern-model-e',
  templateUrl: './modern-model-e.component.html',
  styleUrls: ['./modern-model-e.component.scss']
})
export class ModernModelEComponent implements OnInit {

  constructor(
    public editorService: EditorService,
  ) { }

  ngOnInit(): void {
  }

  editItem(directory: string): void {
    if (!this.editorService.isEditing) {
      return;
    }
    const url: string = `${this.editorService.bucketPath}${this.editorService.door.category.id}-doors/${this.editorService.door.model.id}/${directory}`;
    window.open(url, '_blank');
  }

}
